import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
import {
  useDepartmentTabController,
  useDepartmentTabManage,
} from "../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
const DepartmentTabController = ({ did }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    all_staff: true,
    mentor: true,
    fees: true,
    checklist: true,
    examination: true,
    copo: true,
    malicious: true,
    backlog: true,
    holiday: true,
    setting: true,
    complaint: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [departmentTabController] = useDepartmentTabController();
  const { departmentTabManage } = useDepartmentTabManage({
    data: {
      did: did,
    },
    skip: !did,
  });

  useEffect(() => {
    if (departmentTabManage?.tab_manage) {
      setState({
        all_staff: departmentTabManage?.tab_manage?.all_staff,
        mentor: departmentTabManage?.tab_manage?.mentor,
        fees: departmentTabManage?.tab_manage?.fees,
        checklist: departmentTabManage?.tab_manage?.checklist,
        examination: departmentTabManage?.tab_manage?.examination,
        copo: departmentTabManage?.tab_manage?.copo,
        malicious: departmentTabManage?.tab_manage?.malicious,
        backlog: departmentTabManage?.tab_manage?.backlog,
        holiday: departmentTabManage?.tab_manage?.holiday,
        complaint: departmentTabManage?.tab_manage?.complaint,
        setting: departmentTabManage?.tab_manage?.setting,
      });
    }
  }, [departmentTabManage?.tab_manage]);

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    departmentTabController({
      did: did,
      tabManage: { tab_manage: updatedObj },
    })
      .then(() => {
        setSnackbar({
          msg: "Updated",
          open: true,
          variant: "success",
        });
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <UserMemberContainerWrapper
        customStyle={{
          width: "61.493vw",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("tab_controller")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("all_staff")}
              checkedToggle={state.all_staff}
              onToggleHandler={(e) =>
                onSubmitFormHandler("all_staff", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("mentor")}
              checkedToggle={state.mentor}
              onToggleHandler={(e) =>
                onSubmitFormHandler("mentor", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("fees")}
              checkedToggle={state.fees}
              onToggleHandler={(e) =>
                onSubmitFormHandler("fees", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("checklist")}
              checkedToggle={state.checklist}
              onToggleHandler={(e) =>
                onSubmitFormHandler("checklist", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("examination")}
              checkedToggle={state.examination}
              onToggleHandler={(e) =>
                onSubmitFormHandler("examination", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("copo")}
              checkedToggle={state.copo}
              onToggleHandler={(e) =>
                onSubmitFormHandler("copo", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("malicious")}
              checkedToggle={state.malicious}
              onToggleHandler={(e) =>
                onSubmitFormHandler("malicious", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("backlog")}
              checkedToggle={state.backlog}
              onToggleHandler={(e) =>
                onSubmitFormHandler("backlog", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("holiday")}
              checkedToggle={state.holiday}
              onToggleHandler={(e) =>
                onSubmitFormHandler("holiday", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("complaint")}
              checkedToggle={state.complaint}
              onToggleHandler={(e) =>
                onSubmitFormHandler("complaint", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("setting")}
              checkedToggle={state.setting}
              onToggleHandler={(e) =>
                onSubmitFormHandler("setting", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <div
              style={{
                flex: "1 1 33%",
              }}
            ></div>
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default DepartmentTabController;
